  --text-color-inverse: #f0f0f0;
  --text-shadow-dark: 0 0 10px white;
  --box-shadow-dark: 0 10px 20px rgba(0, 0, 0, .25);
  --primary-color: #007fff;
  --primary-color-alpha: rgba(0, 127, 255, 0.5);
  --primary-color-light: #3399ff;
  --primary-color-dark: #0066cc;
  --primary-color-lighter: #80bfff;
  --primary-color-darker: #004080;
  --primary-text-color: #1f1f1f;
  --secondary-color: #ff7f00;
  --secondary-color-alpha: rgba(255, 127, 0, 0.5);
  --secondary-color-light: #ff9933;
  --secondary-color-dark: #cc6600;
  --secondary-color-lighter: #ffbf80;
  --secondary-color-darker: #804000;
  --secondary-text-color: #f0f0f0;
  --positive-color: #00bf00;
  --positive-color-alpha: rgba(0, 191, 0, 0.5);
  --positive-color-light: #00f200;
  --positive-color-dark: #008c00;
  --positive-color-lighter: #40ff40;
  --positive-color-darker: #004000;
  --positive-text-color: #f0f0f0;
  --negative-color: #bf0000;
  --negative-color-alpha: rgba(191, 0, 0, 0.5);
  --negative-color-light: #f20000;
  --negative-color-dark: #8c0000;
  --negative-color-lighter: #ff4040;
  --negative-color-darker: #400000;
  --negative-text-color: #f0f0f0;
  --overlay-dark: rgba(240, 240, 240, 0.5);
  --bar-gradient: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.5)   0%,
        rgba(255, 255, 255, 0.25)  75%,
        rgba(255, 255, 255, 0) 100%);
  --animation-duration: 250ms;
  --animation-duration-fast: 175ms;
  --animation-duration-slow: 375ms;
  --debug-color: rgba(255,0,255,.25); }

/**
 * Globally shared SCSS, including global variables and mixins.
 * TODO:
 * - later, split into small parts and import
 */
/**
  * Mixin to return a specific PX-value for a given breakpoint-key (as defined above).
  * Mainly used for other mixins to iterate over all breakpoints and to generate CSS-Classes,
  * That way other mixins can reference neutral keys ('xs', 'sm', ...) instead of fixed PX-values.
  */
:global(.--primary) {
  color: var(--primary-color); }
  :global(.--primaryBox) {
    background-color: var(--primary-color-darker);
    border: 1px solid var(--primary-color); }

:global(.--secondary) {
  color: var(--secondary-color); }
  :global(.--secondaryBox) {
    background-color: var(--secondary-color-darker);
    border: 1px solid var(--secondary-color); }

:global(.--success) {
  color: var(--positive-color); }
  :global(.--successBox) {
    background-color: var(--positive-color-darker);
    border: 1px solid var(--positive-color); }

:global(.--error) {
  color: var(--negative-color); }
  :global(.--errorBox) {
    background-color: var(--negative-color-darker);
    border: 1px solid var(--negative-color); }

:global(.--hatched) {
  background: repeating-linear-gradient(45deg, rgba(127, 63, 63, 0.5), rgba(127, 63, 63, 0.5) 1px, rgba(255, 0, 0, 0.1) 1px, rgba(255, 0, 0, 0.1) 20px); }

:global(.svNotification) {
  background-color: transparent;
  color: white;
  width: 100%;
  height: 0;
  margin: 0;
  opacity: 0;
  transition: height var(--animation-duration), opacity var(--animation-duration);
  overflow: hidden;
  display: flex;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent; }
  :global(.svNotification__icon) {
    flex-grow: 0;
    font-size: 3em;
    min-width: 1em;
    padding-top: .25em; }
  :global(.svNotification__content) {
    flex-grow: 1;
    display: flex;
    flex-direction: column; }
    :global(.svNotification__content_title) {
      flex-grow: 0;
      padding-left: .5em;
      padding-right: 1em;
      font-size: 1.25em;
      font-weight: bolder;
      display: flex; }
      :global(.svNotification__content_title_text) {
        flex-grow: 1; }
      :global(.svNotification__content_title_close) {
        flex-grow: 0;
        display: none; }
        :global(.svNotification__content_title_close--visible) {
          display: inline-block; }
    :global(.svNotification__content_text) {
      flex-grow: 1;
      padding-left: 1em; }
  :global(.svNotification--floatable) {
    position: absolute;
    right: 10px;
    margin-top: 10px;
    width: 320px;
    border: 1px solid transparent;
    box-shadow: var(--box-shadow-dark);
    border-radius: 4px; }
  :global(.svNotification--visible) {
    height: 80px;
    opacity: 1; }
  :global(.svNotification--info) {
    background-color: var(--primary-color-darker);
    border-color: var(--primary-color); }
  :global(.svNotification--confirm) {
    background-color: var(--secondary-color-darker);
    border-color: var(--secondary-color); }
  :global(.svNotification--error) {
    background-color: var(--negative-color-darker);
    border-color: var(--negative-color); }
  :global(.svNotification--success) {
    background-color: var(--positive-color-darker);
    border-color: var(--positive-color); }
