<!-- TODO rename to Toggle? -->
<label class="svCheckBox"><input type="{inputType}" {value} {checked} {disabled} {name}><span class="svCheckBox__checkmark"></span><span class="svCheckBox__label"><slot></slot></span></label>

<script>
    export let value = '';
    export let checked = false;
    export let disabled = false;
    export let type = 'check';
    export let name = undefined;

    $: inputType = type === 'check' ? 'checkbox': 'radio';
</script>

