<SideBar><Title size="2">SideBar</Title><Section><Article title="Sidebar Content">Some side bar content</Article></Section><Grid><Tile width="4" height="4" clip="{false}" gap="2px"><FlipPanel><div class="wrapper" slot="front" style="background-color: var(--bg-color);"><div class="AppIcon"><Icon icon="phone-squared"></Icon></div></div><div class="wrapper" slot="back" style="background-color: var(--bg-color-light);"><div class="AppIcon AppIcon--active"><Icon icon="phone-squared"></Icon></div></div></FlipPanel></Tile><Tile width="4" height="4" clip="{false}" gap="2px"><FlipPanel><div class="wrapper" slot="front" style="background-color: var(--bg-color);"><div class="AppIcon"><Icon icon="pinterest-squared"></Icon></div></div><div class="wrapper" slot="back" style="background-color: var(--bg-color-light);"><div class="AppIcon AppIcon--active"><Icon icon="pinterest-squared"></Icon></div></div></FlipPanel></Tile><Tile width="4" height="4" clip="{false}" gap="2px"><FlipPanel><div class="wrapper" slot="front" style="background-color: var(--bg-color);"><div class="AppIcon"><Icon icon="rss-squared"></Icon></div></div><div class="wrapper" slot="back" style="background-color: var(--bg-color-light);"><div class="AppIcon AppIcon--active"><Icon icon="rss-squared"></Icon></div></div></FlipPanel></Tile><Tile width="8" height="4" clip="{false}" gap="2px"><FlipPanel><div class="wrapper" slot="front" style="background-color: var(--bg-color);"><div class="AppIcon"><Icon icon="pencil-squared"></Icon></div></div><div class="wrapper" slot="back" style="background-color: var(--bg-color-light);"><div class="AppIcon AppIcon--active"><Icon icon="pencil-squared"></Icon></div></div></FlipPanel></Tile><Tile width="4" height="4" clip="{false}" gap="2px"><FlipPanel><div class="wrapper" slot="front" style="background-color: var(--bg-color);"><div class="AppIcon"><Icon icon="tumblr-squared"></Icon></div></div><div class="wrapper" slot="back" style="background-color: var(--bg-color-light);"><div class="AppIcon AppIcon--active"><Icon icon="tumblr-squared"></Icon></div></div></FlipPanel></Tile></Grid></SideBar><Dialog></Dialog><Header><NavBar><span slot="title"><img on:click="{sidebar.show}" src="assets/icons/app-icon.jpg" style="width:1.5em;height:1.5em;vertical-align:text-bottom;margin-right:.5em"><span>Svelte Components Test Page</span></span><div slot="icons"><IconBar><Icon title="Facebook" url="http://facebook.com" icon="facebook"></Icon><Icon title="Tumblr" url="http://tumblr.com" icon="tumblr"></Icon><Icon title="Artstation" url="http://artstation.com" icon="help"></Icon><Icon title="DeviantArt" url="http://deviantart.com" icon="deviantart"></Icon></IconBar></div></NavBar><Notification close="2.5s"></Notification><Notification close="click" floatable></Notification></Header><Page><PageBlock><Title size="1">About this page</Title><Section>This is a test page for all SV-Components, to show them in an overview and to test them.<br>
Main target of this little project is to <b style="color:rgb(0,127,0)">mess around, using
Svelte with current CSS/JS-features</b> and <b style="color:rgb(127,0,0);text-decoration:line-through;">
not to be fully-cross-browser-on-all-device-in-space-super-duper-stuff</b>.<br>
If you're looking for examples <b style="color:rgb(0,127,0)">how some stuff can(!) be done (JS/CSS/PUG/SVELTE)</b>, be my guest.<br>
If you're looking for some <b style="color:rgb(127,0,0);text-decoration:line-through;">ready-to-deploy fully-fledged super framework</b> - nah, don't use this here, there are better ones.<br>
Note that areas that are <b class="--hatched">'hatched'</b> in their style, are things to be done.<br>
<br>
For the time being, this code is 'laboratory'-code. That said expect following things:<ul><li>Code might break</li><li>Page might not work in any browser/-version (i'm looking at you IE(11) & Safari)</li><li>Not created under aspects of 'mobile-first'. I'm mean... it might work and do things... or not...</li><li>Not everything is feature complete</li><li>Not everything is (well)documented (here and/or in code)</li><li>Not ment for production... really</li></ul>So, let's go...<br><br>
P.S.: try to click the logo/icon above that will toggle a sidemenu!</Section><Title size="2">Titles</Title><Section><Description title="About titles"><TextBlock title="Syntax:"><Code>Title(size="[SIZE]") [TEXT]</Code></TextBlock><TextBlock title="Parameters:"><Code>
size = 1 | 2 | 3 | 4 | 5 | 6 <Hint info="This attribute is optional."/><br>
</Code></TextBlock><TextBlock title="Examples:"><Title size="1">Title Size 1</Title><Title size="2">Title Size 2</Title><Title size="3">Title Size 3</Title><Title size="4">Title Size 4</Title><Title size="5">Title Size 5</Title><Title size="6">Title Size 6</Title></TextBlock></Description></Section><Title size="2">Buttons</Title><Section><Description title="Single buttons"><TextBlock title="Syntax:"><Code>Button(icon="[ICON]",type="[TYPE]") [TEXT]</Code></TextBlock><TextBlock title="Parameters:"><Code>
icon = [ICON] <Hint info="This attribute is optional."/><br>
type = default | primary | secondary | disabled <Hint info="This attribute is optional."/><br>
</Code></TextBlock><TextBlock title="Examples, default style:"><Button type="default">Default</Button><Button type="default pulse">Default with pulse effect</Button><Button icon="picture" type="default">Default with icon</Button></TextBlock><TextBlock title="Examples, primary style:"><Button type="primary">Primary</Button><Button type="primary pulse">Primary with pulse effect</Button><Button icon="picture" type="primary">Primary with icon</Button></TextBlock><TextBlock title="Examples, secondary style:"><Button type="secondary">Secondary</Button><Button type="secondary pulse">Secondary with pulse effect</Button><Button icon="picture" type="secondary">Secondary with icon</Button></TextBlock><TextBlock title="Examples, disabled style:"><Button type="disabled">Disabled</Button><Button type="disabled pulse">Disabled with pulse effect</Button><Button icon="picture" type="disabled">Disabled with icon</Button></TextBlock></Description><Description title="Buttons in a group panel"><div class="--hatched"><TextBlock title="Examples:"><ButtonGroup>{#each Array(10) as _, i}<Button type="default">Default</Button>{/each}</ButtonGroup></TextBlock></div></Description></Section><Title size="2">Checkboxes</Title><Section><Description title="About checkboxes"><TextBlock title="Syntax:"><Code>CheckBox(checked,disabled) [TEXT]</Code></TextBlock><TextBlock title="Parameters:"><Code>
disabled <Hint info="This attribute is optional.This attribute needs no value, just the key (instead of 'foo=&quot;bar&quot;', just use 'foo')."/><br>
checked <Hint info="This attribute is optional.This attribute needs no value, just the key (instead of 'foo=&quot;bar&quot;', just use 'foo')."/><br>
</Code></TextBlock><TextBlock title="Examples:"><CheckBox>Default unchecked</CheckBox><CheckBox checked>Default checked</CheckBox><br><CheckBox disabled>Disabled unchecked</CheckBox><CheckBox disabled checked>Disabled checked</CheckBox></TextBlock></Description></Section><Title size="2">Radio Buttons</Title><Section><Description title="About radio buttons"><TextBlock>These are just checkboxes just with a slightly different style and behaviour.<br>
<b style="color:red;">TODO Rename CheckBox to Toggle</b></TextBlock><TextBlock title="Syntax:"><Code>CheckBox(type="radio",name="[NAME]") [TEXT]</Code></TextBlock><TextBlock title="Parameters:"><Code>
type = radio<br>
name = [STRING] <Hint info="This attribute is optional."/><br>
disabled <Hint info="This attribute is optional.This attribute needs no value, just the key (instead of 'foo=&quot;bar&quot;', just use 'foo')."/><br>
checked <Hint info="This attribute is optional.This attribute needs no value, just the key (instead of 'foo=&quot;bar&quot;', just use 'foo')."/><br>
</Code></TextBlock><TextBlock title="Examples:"><CheckBox type="radio" name="foo1">Default unchecked</CheckBox><CheckBox type="radio" name="foo1" checked>Default checked</CheckBox><br><CheckBox type="radio" name="foo2" disabled>Disabled unchecked</CheckBox><CheckBox type="radio" name="foo2" disabled checked>Disabled checked</CheckBox></TextBlock></Description></Section><Title size="2">Input fields</Title><Section><Description title="About input fields"><TextBlock title="Syntax:"><Code>Field(value="[VALUE]",placeholder="[TEXT]",type="[TYPE]",disabled) [TEXT]</Code></TextBlock><TextBlock title="Parameters:"><Code>
type = text | password <Hint info="This attribute is optional."/><br>
value = [STRING] <Hint info="This attribute is optional."/><br>
placeholder = [STRING] <Hint info="This attribute is optional."/><br>
disabled <Hint info="This attribute is optional.This attribute needs no value, just the key (instead of 'foo=&quot;bar&quot;', just use 'foo')."/><br>
</Code></TextBlock><TextBlock title="Examples standard:"><Field></Field><Field value="default value"></Field><Field placeholder="Placeholder"></Field><Field placeholder="Placeholder" value="default value"></Field></TextBlock><TextBlock title="Examples standard, disabled:"><Field disabled></Field><Field disabled value="default value"></Field><Field disabled placeholder="Placeholder"></Field><Field disabled placeholder="Placeholder" value="default value"></Field></TextBlock><TextBlock title="Password type:"><Field type="password"></Field><Field type="password" value="default value"></Field><Field type="password" placeholder="Placeholder"></Field><Field type="password" placeholder="Placeholder" value="default value"></Field></TextBlock><TextBlock title="Password type, disabled:"><Field type="password" disabled></Field><Field type="password" disabled value="default value"></Field><Field type="password" disabled placeholder="Placeholder"></Field><Field type="password" disabled placeholder="Placeholder" value="default value"></Field></TextBlock></Description></Section><Title size="2">Dialogs</Title><Section><Description title="About dialogs"><TextBlock>You define just one <b>Dialog</b>-instance in your page, the rest will happen via the
global JS-function <Code>'dialog.show'</Code> and data-attributes on the element
triggering that function.</TextBlock><TextBlock title="Trigger:"><Code>button(on:click='&#123;dialog.show&#125;',data-type="success",data-title="Title",data-text="Text")</Code></TextBlock><TextBlock title="Parameters:"><Code>
data-type : success | error | info | confirm <Hint info="This attribute is optional."/><br>
data-title: [STRING] <Hint info="This attribute is optional."/><br>
data-text : [STRING] <Hint info="This attribute is optional."/><br>
</Code></TextBlock><TextBlock title="Examples:"><button on:click="{dialog.show}" data-title="Some title" data-text="Some text">Show Neutral Dialog</button><button on:click="{dialog.show}" data-type="success" data-title="Success :)" data-text="Success Text">Show Success Dialog</button><button on:click="{dialog.show}" data-type="error" data-title="Error :(" data-text="Success Text">Show Error Dialog</button><button on:click="{dialog.show}" data-type="info" data-title="Info!" data-text="Info Text">Show Info Dialog</button><button on:click="{dialog.show}" data-type="confirm" data-title="Confirm?" data-text="Confirm Text">Show Confirm Dialog</button></TextBlock></Description></Section><Title size="2">Notifications</Title><Section><Description title="About notifications"><TextBlock>You define just <b>Notification</b>-instances in your page, the rest will happen via the
global JS-function <Code>'notification.show'</Code> and data-attributes on the element
triggering that function.</TextBlock><TextBlock>Notifications can appear in two shaping: as <b>badge</b> (default) and as <b>floatable</b>
box. Ideally you define just one instance of each so that you have a maximum of two in your
doc (or just one, if you only need one type). Which notification is triggered is depending
on the <Code>'data-floatable' </Code>-attribute later on.</TextBlock><TextBlock>The Notification itself has an option to define its close-behaviour. You can
either define that it should close by clicking a 'close'-icon or you
can define a auto-close time value (like '1s', '250ms', '2.5s', etc.)</TextBlock><TextBlock title="Options:"><Code>
Notification(close='click')<br>
Notification(close='[FLOAT|INT](ms|s)')<br>
Notification(close='click',floatable)<br>
Notification(close='[FLOAT|INT](ms|s)',floatable)
</Code></TextBlock><TextBlock title="Trigger:"><Code>button(on:click='&#123;notification.show&#125;',data-type="success",data-title="Title",data-text="Text",data-floatable)</Code></TextBlock><TextBlock title="Parameters:"><Code>
data-type     : success | error | info | confirm <Hint info="This attribute is optional."/><br>
data-title    : [STRING] <Hint info="This attribute is optional."/><br>
data-text     : [STRING] <Hint info="This attribute is optional."/><br>
data-floatable <Hint info="This attribute is optional.This attribute needs no value, just the key (instead of 'foo=&quot;bar&quot;', just use 'foo')."/><br>
</Code></TextBlock><TextBlock title="Examples for badge notification:"><button on:click="{notification.show}" data-type="success" data-title="Success :)" data-text="Success Text">Show Success Badge Notification</button><button on:click="{notification.show}" data-type="error" data-title="Error :(" data-text="Error Text">Show Error Badge Notification</button><button on:click="{notification.show}" data-type="info" data-title="Info!" data-text="Info Text">Show Info Badge Notification</button><button on:click="{notification.show}" data-type="confirm" data-title="Confirm?" data-text="Confirm Text">Show Confirm Badge Notification</button></TextBlock><TextBlock title="Examples for floatable notification:"><button on:click="{notification.show}" data-floatable data-type="success" data-title="Success floatable :)" data-text="Success Floatable Text">Show Success Floatable Notification</button><button on:click="{notification.show}" data-floatable data-type="error" data-title="Error floatable :(" data-text="Error Floatable Text">Show Error Floatable Notification</button><button on:click="{notification.show}" data-floatable data-type="info" data-title="Info floatable!" data-text="Info Floatable Text">Show Info Floatable Notification</button><button on:click="{notification.show}" data-floatable data-type="confirm" data-title="Confirm floatable?" data-text="Confirm Floatable Text">Show Confirm Floatable Notification</button></TextBlock></Description></Section><Title size="3">Grid</Title><Section><Description title="About grid"><TextBlock>A grid based on <b>Tile</b>s which can recive a width/height (based on cells from this grid)
and which keep their original defined aspect ratio even at resizing.</TextBlock><TextBlock title="Examples:"><Grid>{#each images as image}<Tile width="{image.width}" height="{image.height}" gap="{gridGap}"><Image url="{image.url}" focal="{image.focal}"></Image><Overlay><Label>{image.desc}</Label><a href="foo" style="pointer-events:all;"><Icon icon="help"></Icon></a></Overlay></Tile>{/each}<Tile width="6" height="3" clip="{false}" gap="{gridGap}"><FlipPanel><div class="wrapper" slot="front" style="box-shadow:0 10px 20px rgba(0,0,0,.25)"><Image url="{images[0].url}" focal="{images[0].focal}"></Image><Overlay><Label>Click to flip</Label><a href="foo" style="pointer-events:all;"><Icon icon="help"></Icon></a></Overlay></div><div class="wrapper" slot="back" style="padding:1em;box-shadow:0 10px 20px rgba(0,0,0,.25);background-color: #ddd;"><Article title="Image Title">{images[0].desc} (Click to flip back)</Article></div></FlipPanel></Tile><Tile width="6" height="3" clip="{false}" gap="{gridGap}"><FlipPanel><div class="wrapper" slot="front" style="box-shadow:0 10px 20px rgba(0,0,0,.25)"><Image url="{images[1].url}" focal="{images[1].focal}"></Image><Overlay><Label>Click to flip</Label><a href="foo" style="pointer-events:all;"><Icon icon="help"></Icon></a></Overlay></div><div class="wrapper" slot="back" style="padding:1em;box-shadow:0 10px 20px rgba(0,0,0,.25);background-color: #ddd;"><Article title="Image Title">{images[1].desc} (Click to flip back)</Article></div></FlipPanel></Tile></Grid></TextBlock></Description></Section><Title size="2">Articles</Title><Section><Description title="About articles"><TextBlock>A simple textblock cappable of showing a title and content, all with a specific style.</TextBlock><TextBlock title="Options:"><Code>
Article(title='My Article')
</Code></TextBlock><TextBlock title="Parameters:"><Code>
title: [STRING] <Hint info="This attribute is optional."/><br>
</Code></TextBlock><TextBlock title="Examples:"><Article title="First Article">Some article text.</Article></TextBlock></Description></Section><Title size="2">Cards</Title><Section><Description title="About cards"><TextBlock>They're based on <b>FlipPanel</b> with a specific style and already prepared, so all you need is
to define the content for the front and back of the card. It uses '<a href="https://svelte.dev/tutorial/named-slots" target="_blank_">named slots</a>'.</TextBlock><TextBlock title="Examples (click on card to flip/back):"><Card><div class="wrapper" slot="image"><Image url="{images[0].url}" focal="{images[0].focal}"></Image></div><span slot="shortDesc">Short Description</span><span slot="longDesc">Long Description</span></Card><Card><div class="wrapper" slot="image"><Image url="{images[1].url}" focal="{images[1].focal}"></Image></div><span slot="shortDesc">Short Description</span><span slot="longDesc">Long Description</span></Card><Card><div class="wrapper" slot="image"><Image url="{images[2].url}" focal="{images[2].focal}"></Image></div><span slot="shortDesc">Short Description</span><span slot="longDesc">Long Description</span></Card></TextBlock></Description></Section><Title size="2">Accordion</Title><Section><Accordion><AccordionEntry title="Tab A">Content A</AccordionEntry><AccordionEntry title="Tab B">Content B</AccordionEntry><AccordionEntry title="Tab C">Content C</AccordionEntry><AccordionEntry title="Tab D">Content D</AccordionEntry><AccordionEntry title="Tab E">Content E</AccordionEntry></Accordion></Section><Title size="2">Wiper</Title><Section><Description title="About wiper"><TextBlock>A wiper is a container holding up 'pages'. When scrolling, the pages will
be 'wiped' to top, revealing the next page. When stopping scrolling - 
and a page is not fully shown - the wiper will automatically slide in/out
the best matching page in this situation.</TextBlock><TextBlock title="Examples:"><div style="position:relative;width:50vw;height:50vh;"><Wiper><WiperPage><Image url="{images[0].url}" focal="{images[0].focal}"></Image></WiperPage><WiperPage><Image url="{images[1].url}" focal="{images[1].focal}"></Image></WiperPage><WiperPage><Image url="{images[2].url}" focal="{images[2].focal}"></Image></WiperPage></Wiper></div></TextBlock></Description></Section><Title size="2">SideBar</Title><Section><Description title="About the sidebar"><TextBlock>There should be just one instance of SideBar in the document.<br>
When added you can toggle the SideBar by calling <Code>'sidebar.show'</Code>.<br>
As container the SideBar can be filled with any kind of content.<br>
It'll stay fixed when scrolling.</TextBlock><TextBlock title="Trigger:"><Code>button(on:click='&#123;sidebar.show&#125;')</Code></TextBlock></Description></Section></PageBlock><PageBlock hatched><Title size="2">Slider</Title><Section>TO BE DONE</Section><Title size="2">Teaser</Title><Section>TO BE DONE</Section><Title size="2">Tabbed pane</Title><Section>TO BE DONE</Section><Title size="2">Code</Title><Section>TO BE DONE</Section><Title size="2">Description</Title><Section>TO BE DONE</Section><Title size="2">Flip panel</Title><Section>TO BE DONE</Section><Title size="2">Hint</Title><Section>TO BE DONE</Section><Title size="2">Icon</Title><Section>TO BE DONE</Section><Title size="2">Icon bar</Title><Section>TO BE DONE</Section><Title size="2">Image</Title><Section>TO BE DONE</Section><Title size="2">Label</Title><Section>TO BE DONE</Section><Title size="2">Notification</Title><Section>TO BE DONE</Section><Title size="2">Overlay</Title><Section>TO BE DONE</Section><Title size="2">Section</Title><Section>TO BE DONE</Section><Title size="2">Text block</Title><Section>TO BE DONE</Section><Title size="2">Tile</Title><Section>TO BE DONE</Section><Title size="2">Title</Title><Section>TO BE DONE</Section><Title size="2">Page</Title><Section>TO BE DONE</Section></PageBlock></Page>

