<!--
    Acts as container to be used along with Grid. A single 'tile' can have a width and height defined
    where the size is related to an amount of 'cells' inside the grid (and not pixels).
    Additionally you can define a 'gap' that is a sort of padding around the tile content and the tile itself.
-->
<div class="svTile {clipped}" data-tileSize="{tileSize}" on:mouseenter="{enter}" on:mouseleave="{leave}" class:hovered><div class="svTile__wrapper"><div class="svTile__wrapper_content" style="{tilePadding}"><div class="svTile__wrapper_content_view"><slot></slot></div></div></div></div>

<script>
    export let clip   = true;
    export let gap    = '0';
    export let height = 1;
    export let width  = 1;

    let hovered = false;

    $: tileSize    = width + ':' + height;
    $: tilePadding = 'padding:' + gap + ';';
    $: clipped     = clip ? '' : 'svTile--unclipped';

    function enter (e) {
        hovered = true;
    }

    function leave (e) {
        hovered = false;
    }
</script>

<style lang="scss" global>:global(.svTile) {
  width: 100%;
  flex-basis: 100%;
  float: left; }
  :global(.svTile__wrapper) {
    width: 100%;
    height: 0;
    position: relative; }
    :global(.svTile__wrapper_content) {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }
      :global(.svTile__wrapper_content_view) {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden; }
  :global(.svTile--unclipped .svTile__wrapper_content_view) {
