<div class="svAccordionEntry {expandedStyle}"><div class="svAccordionEntry__title" on:click="{toggle}">{title}</div><div class="svAccordionEntry__content"><slot></slot></div></div>

<script>
    export let title = 'N/A';

    let expanded = false;

    $: expandedStyle = expanded ? 'svAccordionEntry--expanded' : '';

    function toggle () {
        expanded = !expanded;
        console.log('toggle: '+expanded);

        // TODO later: check if only on entry should be opened at the same time.
    }
</script>

